@import url('https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap');

* {
  margin: 0px;
  padding: 0px;
  font-family: "Work Sans", Arial, Helvetica, sans-serif;
}

#app {
  display: grid;
  grid-template-columns: 0.7fr 3fr;
  grid-template-rows: 1fr;
  height: 100vh;
}

#app aside {
  background: #141677;
  grid-area: 1/1/1/2;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.1fr 0.4fr 0.6fr 0.2fr;
}

#app #logo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

#app #logo img {
  cursor: pointer;
}
 
#app aside #landing {
  grid-area: 2/1/2/2;
}

#app aside #landing img {
  border-radius: 50%;
  width: 125px;
  height: 125px;
}

#app aside #landing h1 {
  color: white;
  text-align: center;
}

#app aside #landing h1 {
  margin-top: 5px;
  font-size: 1.7rem;
  font-weight: 400;
}

#app aside #landing h2 {
  font-weight: 300;
  font-size: 1.2rem;
}

#app aside #alunos {
  color: white;
  grid-area: 3/1/3/2;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

#app aside #logout {
  grid-area: 4/1/4/2;
  height: 35px;
  width: 100px;
  font-size: 1.3rem;
  margin: auto;
  border-radius: 10px;
  color: white;
  background: #83100c;
  border: 0px;
  box-shadow: 2px 2px 1px rgba(0, 0, 0, 0.263);
  cursor: pointer;
}

#app #alunos header {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 10vh;
  border-top: 2px solid #111266;
  border-bottom: 2px solid #111266;
  margin-top: 5px;
}

#app #alunos header h1:after{
  content: "\25bc";
  font-size: 0.9rem;
  margin-left: 5px;
  color: white;
}

#app nav {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#app nav .crud-alunos {
  border: 0px;
  padding: 15px 0px;
  width: 70%;
  border-bottom: 2px solid #111266;
  cursor: pointer;
  font-weight: 300;
  color: white;
}

#app nav .crud-alunos:not(:hover) h1 {
  transition: all 0.3s;
}

#app nav .crud-alunos:hover h1 {
  transform: scale(1.1);
  transition: all 0.3s;
}

#app nav .crud-alunos:hover {
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.16);
  background: #111266;
  transition: all 0.3s;
  padding: 20px 0px;
}

#app nav .cadastrar:hover {
  background: rgb(30, 169, 30);
}

#app nav .alterar:hover {
  background: rgb(177, 177, 60);
}

#app nav .remover:hover {
  background: #83100c;
}

#app nav .crud-alunos:not(:hover) {
  transition: all 0.3s;
  background: transparent;
}

#app main {
  grid-area: 1/2/1/3;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
#app table {
  border-collapse: collapse;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.342);
  width: 100%;
}

#app table td,th {
  border: 2px solid black;
  font-size: 1rem;
}

#app table td {
  padding: 2px 8px;
}

#app .cep-cell {
  font-size: 1rem;
}

#app table th {
  border-bottom: 0px;
}

#app thead {
  background: #83100c;
  color: white;
}
#app thead th {
  padding: 5px 10px;
}
#app tbody > tr:nth-child(even) {
  background: #952C2C;
}
#app tbody > tr:nth-child(odd) {
  background: #952c2cae;
}

#app tbody > tr td {
  color: white;
  text-align: center;
}